.button {
  box-sizing: border-box;
  color: #0078db;
  cursor: pointer;
  z-index: 1;
  background-color: #0000;
  border: 2px solid #0078db;
  border-radius: 6px;
  width: max-content;
  padding: 14px 28px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.button:before {
  content: "";
  z-index: -1;
  background-color: #0078db;
  width: 100%;
  height: 100%;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.button:hover {
  color: #fff;
}

.button:hover:before {
  transform: translateX(0);
}

